
$(document).ready(function() {
	
	$(".fancybox").fancybox();
	var App = function (){
		$('[data-phone="true"]').mask("+7 (999) 999-99-99");
		$('[data-item="slider"]').owlCarousel({
			loop: true,
			margin: 0,
			nav: true,
			dots: true,
			items: 1,
			navContainer: '.main-navigation',
			dotsContainer: '.main-dots',
			autoplayHoverPause: true,
			autoplay: false,
			navSpeed: 500,
			dotsSpeed: 500,
			dots: true,
		});
		
		var stackModal = 0;

		$(document).on('show.bs.modal', '.modal', function (event) {
			var zIndex = 1040 + (10 * $('.modal:visible').length);
			$(this).css('z-index', zIndex);
			stackModal++;
			setTimeout(function() {
				$('.modal-backdrop').not('.modal-stack').css('z-index', zIndex - 1).addClass('modal-stack');
			}, 0);
		});
		$(document).on('hidden.bs.modal', '.modal', function (event) {
			stackModal--;
			if(stackModal > 0) {
				$('body').addClass("modal-open");
			} else {
				$('body').removeClass("modal-open");
			}
		});

		$('a.smooth').click(function(){
			$('html, body').animate({
				scrollTop: $( $.attr(this, 'href') ).offset().top - 100
			}, 1000);
			return false;
		});

		var svg = document.querySelector('svg');

		if (svg) {
			let items = [];
			let Item = [];
			let scale = svg.querySelectorAll('.scale');
			let clickHandler = (evt) => {
				let newtarget = evt.currentTarget || event.currentTarget;
				console.log(newtarget);
			}
			Array.prototype.forEach.call(scale, (item) => {
				Item.push(item);
				item.addEventListener("mouseenter", clickHandler, false);
			});
			items.push(...Item);
			console.log(items);
		}
		
		var Form = {
			ValidationOptions: {
				framework: 'bootstrap',

				locale: 'ru_RU',
				
				fields: {
					
					userPhoneNumber: {
						trigger: 'keyup keydown',
						validators: {
							callback: {
								message: 'Это не похоже на телефон!',
								callback: function($field){
									if(/[+][7]\s[()][0-9]{3}[)]\s[0-9]{3}[-][0-9]{2}[-][0-9]{2}/i.test($field) === true){
										return true;
									} else {
										return false;
									}
								}
							},
							notEmpty: {
								message: 'Не заполнено поле «Телефон»'
							},
							blank: {}
						}
					},
					userName: {
						trigger: 'keyup keydown',
						validators: {
							notEmpty: {
								message: 'Не заполнено поле «ФИО»'
							},
							blank: {}
						}
					},
					userAddress: {
						trigger: 'keyup keydown',
						validators: {
							notEmpty: {
								message: 'Не заполнено поле «Город»'
							},
							blank: {}
						}
					},
					userDirection: {
						trigger: 'keyup keydown',
						validators: {
							notEmpty: {
								message: 'Не заполнено поле «Направление»'
							},
							blank: {}
						}
					},
					userEmail: {
						trigger: 'keyup keydown',
						validators: {
							emailAddress: {
								message: 'Это не похожу на E-mail'
							},
							notEmpty: {
								message: 'Не заполнено поле «E-mail»'
							},
							blank: {}
						}
					},
					time: {
						trigger: 'keyup keydown change',
						validators: {
							notEmpty: {
								message: 'Не заполнено поле «Время»'
							},
							blank: {}
						}
					}
				}
			},

			initialize : function () {
				this.Validation('form');
			},
			Validation:function(form){
				$(form).formValidation(this.ValidationOptions)
				.on('success.form.fv', function(e) {
					e.preventDefault();
					var $form = $(e.target),
					fv = $form.data('formValidation');

					$form.ajaxSubmit({
						success: function(responseText, statusText, xhr, $form) {
							console.log(responseText,statusText,xhr,$form);
							$('#call2').modal('hide');
							$('#call').modal('hide');
							$('#thx').modal('show');
						}
					});
				});
			}
		};
		Form.initialize();
	}
	App();
});

